import MasterPage from '../components/Master';
import Precio from '../components/Precio'
import fetch from 'isomorphic-unfetch';
import Noticias from '../components/Noticias'

const Index = (props) => (
    <MasterPage>
        <div className="row">
            <div className="col-12">
                <h2>Precio del bitcoin</h2>
                <Precio
                    precio={props.precioBitcoin} 
                />
            </div>

            <div className="col-md-8">
                <h2>Noticias sobre bitcoin</h2>
                <Noticias
                    noticias={props.noticias}
                />
            </div>
            <div className="col-md-4">
                <h2>Proximos eventos bitcoin</h2>
                {console.log(props.eventos)}
            </div>
        </div>

    </MasterPage>
);

//Lo que hace getInitialProps es que una vez que carga, hace la consulta a la Api y lo guarda como props
Index.getInitialProps = async () => {
    //como son 3 llamados con async await, no importa si uno tarda mas que otro
    const precio = await fetch('https://api.coinmarketcap.com/v2/ticker/1/');
    const noticias = await fetch('https://newsapi.org/v2/everything?q=bitcoin&from=2019-09-30&sortBy=publishedAt&apiKey=83fed2872d7a48b89a96e804cddefe0b&language=es')
    const eventos = await fetch('https://www.eventbriteapi.com/v3/events/search/?q=Bitcoin&sort_by=date&location.address=Mexico&token=4M7VDFZWO3VAGXJ2S7UU')
    
    //retorna una respuesta y hay que poner que sea como JSON
    const resPrecio = await precio.json();
    const resNoticias = await noticias.json();
    const resEventos = await eventos.json();

    return {
        precioBitcoin: resPrecio.data.quotes.USD,
        noticias : resNoticias.articles,
        eventos : resEventos
    }
}


export default Index;